<template>
    <div>
        <basic-header :items="['系统管理','轮播图管理']"></basic-header>
        <el-card style="margin-top: 30px">
            <el-tabs v-model="activeName">
                <el-tab-pane label="轮播图查看" name="first">
                    <el-table :data="tableData">
                        <el-table-column prop="id" label="ID"></el-table-column>
                        <el-table-column prop="url" label="banner图">
                            <template #default="o">
                                <img :src="o.row.url" alt="" width="100" height="100">
                            </template>
                        </el-table-column>
                        <el-table-column prop="sort" label="banner排序"></el-table-column>
                        <el-table-column prop="create_time" label="创建时间"></el-table-column>
                        <el-table-column prop="id" label="所对应的商品id"></el-table-column>
                        <el-table-column label="操作">
                            <template #default="obj">
                                <el-button type="text" @click="edit(obj.row.id)">编辑</el-button>
                                <el-button type="text" @click="del(obj.row.id)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="轮播图添加" name="second">
                    <el-form label-width="100px" ref="bannerForm" :model="bannerForm" :rules="rules">
                        <el-form-item label="选择商品" prop="gid">
                            <el-input v-model="bannerForm.gid" :clearable="true" placeholder="请选择商品">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="banner排序" prop="sort">
                            <el-input v-model="bannerForm.sort"></el-input>
                        </el-form-item>
                        <el-form-item label="轮播图" prop="banner">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="uploadUrl"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="bannerForm.url" :src="bannerForm.url" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submit">添加</el-button>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-dialog
                        title="提示"
                        :visible.sync="dialogVisible"
                        width="80%"
                >
                    <el-form label-width="80px" ref="editorForm" :model="editorForm" :rules="rules2">
                            <el-form-item label="商品名称" prop="gid">
                                <el-input v-model="editorForm.gid"></el-input>
                            </el-form-item>
                            <el-form-item label="商品排序" prop="sort">
                                <el-input v-model="editorForm.sort"></el-input>
                            </el-form-item>
                            <el-form-item label="商品缩略图" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="uploadUrl"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess1"
                                    :before-upload="beforeAvatarUpload">
                                <img v-if="editorForm.url" :src="editorForm.url" class="avatar">
                                <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editorSubmit">确 定</el-button>
  </span>
                </el-dialog>
            </el-tabs>
        </el-card>
    </div>
</template>

<script>
    import MBXhead from "../../components/MBXhead";

    export default {
        data() {
            return {
                activeName: "first",
                tableData: [],
                api: "/api/banner/banner",
                page: 1,
                pageSize: 5,
                total: 0,
                bannerForm: {
                    id:"",
                    gid: "",
                    sort: "",
                    url: ""
                },
                rules: {
                    gid: {required: true, message: '请输入产品名称', trigger: 'change'},
                    sort: {required: true, message: '请输入产品名称', trigger: 'blur'},
                    url: {required: true, message: '请输入产品名称'},
                },
                uploadUrl: "/api/upload/upload",
                dialogVisible:false,
                editorForm:{
                    id:"",
                    gid: "",
                    sort: "",
                    url: ""
                },
                rules2:{
                    gid: {required: true, message: '请输入产品名称', trigger: 'change'},
                    sort: {required: true, message: '请输入产品名称', trigger: 'blur'},
                    url: {required: true, message: '请输入产品名称'},
                }

            }
        },
        methods: {
            fetchData() {
                this.axios.get(this.api, {
                    params: {
                        page: this.page,
                        pageSize: this.pageSize
                    }
                }).then(res => {
                    this.tableData = res.data.data;
                    this.total = res.data.total;
                })
            },
            handleAvatarSuccess(res) {
                this.bannerForm.url = res;
                this.$refs.bannerForm.validateField("url");
            },
            handleAvatarSuccess1(res){
                this.editorForm.url = res;
                this.$refs.editorForm.validateField("url");
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt500kb = file.size / 1024 / 500;

                if (!isJPG) {
                    this.$message.error('上传图片只能是 JPG 格式!');
                }
                if (!isLt500kb) {
                    this.$message.error('上传图片大小不能超过 500kb');
                }
                return isJPG && isLt500kb;
            },
            del(id){
                this.$confirm('此操作将永久删除该分类，是否继续？', '提示', {
                    confirmButtonText: "取消",
                    confirmButtonText: "确定",
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then((res) => {
                        if (res.data.code == 200) {
                            this.$message.success("删除成功");
                            this.fetchData()
                        } else {
                            this.error(res.data.msg)
                        }
                    })
                }).catch(() => {
                    this.$message.info("已取消删除")
                });
            },
            edit(id){
                this.dialogVisible = true;
                this.axios.get(this.api, {params: {id: id}}).then(res => {
                    // console.log(res);
                    this.editorForm = res.data.data;
                })
            },
            editorSubmit(){
                this.$refs.editorForm.validate(valid=>{
                    if(valid){
                        delete this.editorForm.create_time;
                        this.axios.put(this.api, this.editorForm).then(res => {
                            if (res.data.code === 200) {
                                this.dialogVisible=false
                                this.fetchData();
                            }
                        })
                        this.$message.success("修改成功")
                    }else{
                        this.$message.error("验证失败.请检查字段内容")
                    }
                })
            },
            submit() {
                this.$refs.bannerForm.validate(valid => {
                    if (valid) {
                        this.axios.post(this.api, this.bannerForm).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("添加成功");
                                this.$refs.bannerForm.resetFields();
                                this.activeName = "first";
                                this.fetchData();
                            }
                        })
                    } else {
                        this.$message.error("验证失败")
                    }
                })
            }
        },
        watch: {
            page: function () {
                this.fetchData()
            },
        },
        created() {

        },
        components: {
            "basic-header": MBXhead
        },
        mounted() {
            this.fetchData()
        },
    }
</script>
<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>